.lines {
  position: absolute;
}

.lines path {
  stroke: #edeced;
  stroke-width: 2px;
  fill: none;
}

.lines path:hover {
  stroke: #419bf9;
}

.node {
    position: relative;
    border-radius: 2px;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
    color: peru;
    font-size: 30px;
    margin: 0 12px;
    flex-shrink: 0;
}
.tree {
    text-align: center;
    margin-right: 24px;
}

.tree .nodes {
    display: flex;
    margin: 0 12px;
}

.tree .level {
    display: flex;
    justify-content: center;
    position: relative;
}

.tree .level:not(:last-child) {
    margin-bottom: 60px;
}
body {
  background-color: darkslategrey;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.app {
  height: 100vh;
  padding: 24px;
}

.app .trees {
  display: flex;
  margin: 24px;
}

.app h3 {
  color: moccasin;
}
